.slick-slide {
  text-align: center;
  background-color: #222;
  color: #fff;
  min-height: 200px;
  position: relative; }
  .slick-slide .content-wrapper {
    display: inline-block;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%; }
  .slick-slide h2 {
    color: #DDBB00;
    font-weight: normal;
    margin-bottom: 0; }
  .slick-slide h3 {
    position: relative;
    display: inline-block; }
    .slick-slide h3.tennis:before, .slick-slide h3.basketball:before, .slick-slide h3.football:before, .slick-slide h3.default:before {
      content: "";
      display: inline-block;
      position: absolute;
      left: -20px;
      top: 2px;
      width: 16px;
      height: 16px; }
    .slick-slide h3.tennis:before {
      background: url("../../images/icons/tennis.png"); }
    .slick-slide h3.basketball:before {
      background: url("../../images/icons/basketball.png"); }
    .slick-slide h3.football:before {
      background: url("../../images/icons/football.png"); }
    .slick-slide h3.default:before {
      background: url("../../images/icons/default.png"); }
  .slick-slide .date {
    color: #999;
    margin-top: 0; }
  .slick-slide .btn-green {
    color: #fff;
    text-decoration: none;
    display: inline-block;
    background: #3AAA35;
    font-size: 14px;
    font-weight: bold;
    padding: 6px 15px;
    border-radius: 3px;
    margin: 0 0 20px; }



